@import "../node_modules/bootstrap/scss/functions";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,500&display=swap');



body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
}

.container nav {
    padding-right: 0;
    padding-left: 0;
    background: white;

}

.navbar {
    height: 50px;
    background: #654321;
    color: white;
    padding-top: 0;
    display: flex;
    font: black;

}






.hero-bg {
    background: #654321;
    color: white;
    text-align: center;
    padding-bottom: 4em;
}



.hero-bg a {
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 1.2rem;
}

section {
    margin: 0 1em;
}


h1 {
    font-size: 2.5rem;
    text-align: center;
    margin: 2em 0 1.2em;
}

h1 span {
    text-transform: uppercase;
    display: block;
    font-size: 1.5rem;
    position: relative;
    z-index: 1;
    text-align: center;
    
}


.hero-bg p {
    font-weight: bold;
    margin: 0 1em 3em; 
}

.form-container {
    background: #fafafa;
    margin: 2em -1em 0;
    padding: 2em;
}

label {
    color:#654321;
    display: block;
    margin-bottom: 1em;
    font-size: 1.2em;
    padding-top: 1em;

}

input[type=text] {
    border: 1.2px solid #654321;
    width: 100%;
    padding: 1em;
    border-radius: .5em;
    margin-top: 1.2em;
}


.form-container p {
    color: gray;
    margin-bottom: 1.5em;
    font-weight: normal;
    font-size: .9em;
    margin-top: .3em;
}

input[type=button] {
    background-color:rgba(255, 238, 0, 0.938);
    border: none;
    width: 100%;
    color: white;
    font-weight: bold;
    padding: 1em 0;
    margin-bottom: 1.5em;
    margin-top: 1.2em;
    border-radius: .5em;
    font-size: 1.3em;
    cursor: pointer;
    text-shadow: 2px 2px 1px #000;
    
}

input[type=button]:hover {
    background-color: gray;
}

.authentic {
    margin: 0;
}

.middle-col {
    text-align: center;
    margin: 3em 1em;
}

h2 {
    text-transform: uppercase;
    position: relative;
}

h2::before {
    content: ' ';
    position: absolute;
    width: 2em;
    background: rgba(255, 238, 0, 0.938);
    height: .4em;
    bottom: 0;
    z-index: -1;
    margin-left: -.3em;
}

.carousel-inner > .item > img {
    margin: 0 auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}




img {
    width: 100%;
}

p {
    line-height: var(--leading);
}

.overlay {
    position: absolute;
    height: 100vh;
    width: 100%;
    background: rgba(0,0,0,.9);
    z-index: 1;
    place-content: center;
    color: white;
    display: none;
    opacity: 0;
}

.animate-overlay {
    animation: overlay 3s forwards;
}




@media (min-width: 730px) {

    section {
        margin: 0 4em;
    }
    .form-container {
        margin: 2em -4em 0;
    }

}

@media (min-width: 930px) {

    .hero-bg {
        text-align: left;
    }

    .hero-bg p {
        margin: 0 0 3em;
    }

    .hero-bg section {
        width: 65%;
    }

    .form-container {
        margin: 2em 0 0;
        padding: 2em;
        border-radius: .5em;
        box-shadow: 10px 10px 10px rgba(0,0,0,.3);
    }

    form {
        display: flex;
    }
    .form-left {
        width: 70%;
    }
    label {
        margin: 0;
    }
    input[type="button"] {
        height: fit-content;
        font-size: 1.1em;
        margin-left: 1em;
        margin-top: 2.2em;
        padding: .7em 0;
        width: 30%;
    }
    .hero-bg p {
        margin-bottom: 0;
    }

    section.authentic {
        display: grid;
        grid-template-columns: repeat(2, 50%);
        grid-template-areas:
            "bowl content";
    }

    .right-col {
        grid-area: content;
        padding: 0 10%;
        text-align: left;
        align-self: center;
    }

    img {
        grid-area: bowl;
    }

    h2 {
        margin: 0;
    }

}

@media (min-width: 1275px) { 
    .hero-bg section {
        width: 45%;
        margin: 0 15%;
    }
}

.top {
    animation: fadeIn 2s forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-2em);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes overlay {
    0% {
        opacity: 0;
    }
    30% {
        opacity: 1;
    }
    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}



















h3 {
    text-transform: uppercase;
    position: relative;
    margin: 0;
    
}

h3::before {
    content: ' ';
    position: absolute;
    width: 2em;
    background: rgba(255, 238, 0, 0.938);
    height: .4em;
    bottom: 0;
    z-index: -1;
    margin-left: -.3em;
}

.carousel-inner {
    width: 100%;
    height: 100%;
    position: center;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0em;
    align-items: center;
    
}














